<template>
  <div class="login_module">
    <div style="margin: 0 20px">
      <p class="web-font">webVpn</p>

      <el-tabs v-model="activeName" @tab-click="handleClick" :stretch="true">
        <!-- 用户登录标签内容 -->
        <el-tab-pane label="用户登录" name="login">
          <el-form
            :model="ruleForm"
            status-icon
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
          >
            <el-form-item label-width="0" prop="account">
              <el-input
                placeholder="请输入账号"
                v-model="ruleForm.account"
                maxlength="8"
              >
                <template #prepend>
                  <avatar style="width: 1em; height: 1em; margin-right: 8px" />
                </template>
              </el-input>
            </el-form-item>
            <el-form-item label-width="0" prop="password">
              <el-input
                placeholder="请输入密码"
                show-password
                autocomplete="new-password"
                v-model="ruleForm.password"
              >
                <template #prepend>
                  <Promotion
                    style="width: 1em; height: 1em; margin-right: 8px"
                  />
                </template>
              </el-input>
            </el-form-item>
            <div style="overflow: hidden">
              <el-checkbox v-model="checked" class="checkbox"
                >记住密码</el-checkbox
              >
            </div>
            <el-button
              class="login"
              size="medium"
              type="primary"
              @click="login('ruleForm')"
              >登录
            </el-button>
          </el-form>
        </el-tab-pane>
        <!-- 管理员登录 -->
        <el-tab-pane label="管理员登录" name="adminLogin">
          <el-form
            :model="adminRuleForm"
            status-icon
            :rules="adminRules"
            ref="adminRuleForm"
            label-width="100px"
          >
            <el-form-item label-width="0" prop="adminAccount">
              <el-input
                placeholder="请输入管理员账号"
                v-model="adminRuleForm.adminAccount"
              >
                <template #prepend>
                  <avatar style="width: 1em; height: 1em; margin-right: 8px" />
                </template>
              </el-input>
            </el-form-item>
            <el-form-item label-width="0" prop="adminPassword">
              <el-input
                placeholder="请输入管理员密码"
                show-password
                autocomplete="new-password"
                v-model="adminRuleForm.adminPassword"
              >
                <template #prepend>
                  <Promotion
                    style="width: 1em; height: 1em; margin-right: 8px"
                  />
                </template>
              </el-input>
            </el-form-item>
            <div style="overflow: hidden">
              <el-checkbox v-model="adminChecked" class="checkbox"
                >记住密码</el-checkbox
              >
            </div>
            <el-button
              class="login"
              size="medium"
              type="primary"
              @click="adminLogin('adminRuleForm')"
              >登录
            </el-button>
          </el-form>
        </el-tab-pane>
      </el-tabs>
    </div>
    <div class="navTable">
      <!-- <span @click="forgetPassword()">忘记密码</span> -->
      <!-- <span><router-link to="/login/register">注册</router-link></span> -->
    </div>
  </div>
</template>


<script>
import { Avatar, Promotion } from "@element-plus/icons";

export default {
  name: "LoginCommon",
  components: {
    Avatar,
    Promotion,
  },
  data() {
    // 校验方法
    var Account = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("账号不能为空"));
      }
      setTimeout(() => {
        if (!Number.isInteger(Number(value))) {
          callback(new Error("请输入数字值"));
        } else {
          callback();
        }
      }, 1000);
    };
    var Password = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };

    return {
      // 普通用户表单数据
      ruleForm: {
        account: "",
        password: "",
      },
      // 用户校验规则
      rules: {
        account: [{ validator: Account, trigger: "blur" }],
        password: [{ validator: Password, trigger: "blur" }],
      },
      // 管理员表单数据
      adminRuleForm: {
        adminAccount: "",
        adminPassword: "",
      },
      // 管理员数据校验规则
      adminRules: {
        adminAccount: [{ validator: Account, trigger: "blur" }],
        adminPassword: [{ validator: Password, trigger: "blur" }],
      },
      checked: false,
      adminChecked: false,
      activeName: "login",
    };
  },
  mounted() {
    var params = window.location.href.split("?")[1];
    // 路由使用hash 模式 无法使用 localstion.search
    if (params) {
      console.log(params);
      if ((params.split("=")[1] == "adminLogin")) {
        this.activeName = "adminLogin";
      }
    }

    // 判断本地是否保存密码
    var userAccount = this.$getCookie('userAccount');
    var adminAccount = this.$getCookie('adminAccount');
    if(userAccount){
      this.checked = true;
      this.ruleForm.account = this.$token.verify(userAccount).decoded.Account;
      this.ruleForm.password = this.$token.verify(userAccount).decoded.password;
    }
    if(adminAccount){
      this.adminChecked = true;
      this.adminRuleForm.adminAccount = this.$token.verify(adminAccount).decoded.Account;
      this.adminRuleForm.adminPassword = this.$token.verify(adminAccount).decoded.password;
    }
  },
  methods: {
    // 切换标签页
    handleClick(tab, event) {
      console.log(tab, event);
      if (tab.index == 0) {
        window.location.href =
          window.location.href.split("?")[0] + "?type=" + "login";
      } else {
        window.location.href =
          window.location.href.split("?")[0] + "?type=" + "adminLogin";
      }
    },
    // 普通用户登录
    login(formName) {
      // 校验表单数据
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log("普通用户登录");
          // 发送请求
          this.$fetch
            .login({
              user_number: this.ruleForm.account,
              user_pwd_md5: this.$md5(this.ruleForm.password),
            })
            .then((res) => {
              if (res.success) {
                this.$message({ message: "登录成功", type: "success" });
                // 保存token。有效时间为24h
                localStorage.setItem("token", res.token);
                // 调用记住密码功能
                if (this.checked) {
                  this.rememberPassword("user");
                }else{
                  this.$delCookie('userAccount')
                }
                this.$router.push("/");
              }
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 管理员登录
    adminLogin(formName) {
      // 校验表单数据
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$fetch
            .adminLogin({
              name: this.adminRuleForm.adminAccount,
              pwd_md5: this.$md5(this.adminRuleForm.adminPassword),
            })
            .then((res) => {
              if (res.success) {
                this.$message({ message: "登录成功", type: "success" });
                // 保存token。有效时间为24h
                localStorage.setItem("token", res.token);
                // 调用记住密码功能
                if (this.adminChecked) {
                  this.rememberPassword("admin");
                }else{
                  this.$delCookie('adminAccount')
                }

                this.$router.push("/");
              }
            });
        }
      });
    },
    // 记住密码
    rememberPassword(type) {
      if (type == "admin") {
        // 管理员账号
        var adminToken = this.$token.create({
          Account: this.adminRuleForm.adminAccount,
          password: this.adminRuleForm.adminPassword,
          admin: true,
        });
        this.$setCookie("adminAccount", adminToken, 1);
      } else if (type == "user") {
        var userToken = this.$token.create({
          Account: this.ruleForm.account,
          password: this.ruleForm.password,
        });
        this.$setCookie("userAccount", userToken, 1);
      }
    }
  },
};
</script>

<style lang="scss">
.login_module {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 450px;
  height: 380px;
  margin: auto;
  background-color: #f4f5f7 !important;
  border-radius: 5px;
  padding: 15px 0;
  box-sizing: border-box;
  p {
    text-align: center;
    padding: 15px 0 10px;
    margin-bottom: 10px;
    font-size: 30px;
  }
  .checkbox {
    float: left;
    vertical-align: middle;
    padding: 10px;
  }
  .login {
    display: block;
    margin: auto;
  }
  .navTable {
    position: absolute;
    bottom: -28px;
    color: white;
    width: 100%;
    & > span:nth-child(1) {
      float: left;
    }
    & > span:nth-child(2) {
      float: right;
    }
    a {
      text-decoration: none;
      color: #f4f5f7;
    }
  }
  .el-tabs__item {
    font-size: 16px;
    padding: 8px;
    height: auto;
  }
}
</style>